import React, {Component} from 'react';
import './../../../styles/login.scss';
import {check_mark_svg} from './../../../images';
import {Link} from 'react-router-dom';
import LoginPage from './../../Containers/LoginPage';

class NewPasswordDone extends Component {
    render() {
        return (
            <LoginPage>
                <h3>Reset Password Successful</h3>
                <img src={check_mark_svg} alt="check" />
                <Link to="/login" className="btn btn-primary btn-block">Back to Login</Link>                    
            </LoginPage>
        );
    }
}

export default NewPasswordDone;