import React, {
    Component
} from 'react';

import './../../../styles/login.scss';
import LoginPage from './../../Containers/LoginPage';
import {notify} from './../../../utility';
import {authApi} from './../../../api';

class NewPassword extends Component {

    constructor(props){
        super(props);

        this.handleLoginClick = this.handleLoginClick.bind(this);
        this.passwordInput = React.createRef();
        this.confirmNewPassword = React.createRef();
    }

    handleLoginClick(e){
        e.preventDefault();

        const input = {
            newPassword:this.passwordInput.current.value,
            confirmNewPassword:this.confirmNewPassword.current.value,
        }

        if(!input.newPassword){
            notify.error('password is empty!');
            return;
        }

        if(!input.confirmNewPassword){
            notify.error('confirm password is empty!');
            return;
        }

        if(input.newPassword != input.confirmNewPassword){
            notify.error('password != confirm new password');
            return;
        }

        authApi.restPassword(input).then(res => {
            if(res.data){
                notify.success('password changed!');
                this.props.history.push('/newPasswordDone');
            }else{
                notify.error('rest password fail!');
            }
        })

    }

    render() {
        return (
                <LoginPage>
                    <h3>Create New Password</h3>
                    <input id="newPassword" ref={this.passwordInput} autoComplete="false" type="password" placeholder="New Password" />
                    <input id="confirmNewPassword" ref={this.confirmNewPassword} autoComplete="false" type="password" placeholder="Confirm New Password" />
                    <hr />
                    <button onClick={this.handleLoginClick} type="button" className="btn btn-primary btn-block">
                        Reset Password
                    </button>
                </LoginPage>
        );
    }
}

export default NewPassword;