import {UI_BLOCK_START, UI_BLOCK_END} from "./../constants/actionTypes";

const uiBlock = (state = false, action) => {
    console.log('action :', action);
    switch (action.type) {
        case UI_BLOCK_START:
            return {
                blockers: action.blockers
            };
        case UI_BLOCK_END:
            return {
                blockers: action.blockers
            };
        default:
            return {
                blockers: action.blockers
            };
    }
}

export default uiBlock;
