
import data from './../data.json';
import _ from 'lodash';

export const staffData = (page = 1) => {
	//const url = 'http://jsonplaceholder.typicode.com/users';
	//return api.get(url);
	return Promise.resolve(
		{
			data: getPaginatedItems(data.staff, page)
		});
	//return ;
}

const getPaginatedItems = (items, page = 0) => {
	console.log('page :', page);
	const per_page = 3;
	const offset = (page) * per_page;
	const paginatedItems = _.take(_.drop(items, offset), per_page);

	return {
		page: page,
		per_page: per_page,
		total: items.length,
		total_pages: Math.ceil(items.length / per_page),
		data: paginatedItems
	};
}