import React, {Component} from 'react';
import ReactModal from 'react-modal';
import ContentPage from './../Containers/ContentPage';
import './../../styles/patientsDetails.scss';
import './../../styles/modal.scss';

class PatientsDetails extends Component {

    constructor() {
        super();
        this.state = {
            showModal: false
        };

        this.handleOpenModal = this.handleOpenModal.bind(this);
        this.handleCloseModal = this.handleCloseModal.bind(this);
    }

    handleOpenModal() {
        this.setState({
            showModal: true
        });
    }

    handleCloseModal() {
        this.setState({
            showModal: false
        });
    }

    render() {
        return (
            <ContentPage pageTitle="Details">
                    <div className="main_part_header">
                        <div>
                            <button className="btn btn-primary mr10" onClick={this.handleOpenModal} >Edit</button>
                            <button className="btn btn-primary">Delete</button>
                        </div>
                    </div>
                    <div className="dashboard">
                        <div className="dashboard_item dashboard_item-md-3">
                            <div className="card info_card">
                                <p>Anna Walker</p>
                                <h4>User Details*</h4>
                                <table>
                                    <tbody>
                                        <tr>
                                            <td className="pb10">D.O.B</td>
                                            <td className="pb10">27/01/1985</td>
                                        </tr>
                                        <tr>
                                            <td>Ward</td>
                                            <td>1C</td>
                                        </tr>
                                        <tr>
                                            <td>Gender</td>
                                            <td>M</td>
                                        </tr>
                                        <tr>
                                            <td>Smoker</td>
                                            <td>Yes</td>
                                        </tr>
                                        <tr>
                                            <td>Height</td>
                                            <td>5ft 9in</td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                            <div className="card info_card">
                                <h3>User name</h3>
                                <table>
                                    <tbody>
                                        <tr>
                                            <td>User name:</td>
                                            <td>Name1234</td>
                                        </tr>
                                        <tr>
                                            <td>Password:</td>
                                            <td>Abc123</td>
                                        </tr>
                                    </tbody>
                                </table>
                                <a href="/">Generate new password</a>
                            </div>
                        </div>
                        <div className="dashboard_item dashboard_item-md-9">
                            <div className="card result_card">
                                <h3>Results</h3>
                                <div className="table-responsive-md">
                                    <table className="table">
                                        <thead className="thead-light">
                                        <tr>
                                            <th scope="col">Date</th>
                                            <th scope="col">Time</th>
                                            <th scope="col">Temperature (℃)</th>
                                            <th scope="col">PH Level</th>
                                            <th scope="col"></th>
                                        </tr>
                                        </thead>
                                        <tbody>
                                        <tr>
                                            <td>-------------</td>
                                            <td>-------------</td>
                                            <td>20</td>
                                            <td>1</td>
                                            <td><a href="/" className="btn btn-primary">View</a></td>
                                        </tr>
                                        <tr>
                                            <td>-------------</td>
                                            <td>-------------</td>
                                            <td>20</td>
                                            <td>1</td>
                                            <td><a href="/" className="btn btn-primary">View</a></td>
                                        </tr>
                                        <tr>
                                            <td>-------------</td>
                                            <td>-------------</td>
                                            <td>20</td>
                                            <td>1</td>
                                            <td><a href="/" className="btn btn-primary">View</a></td>
                                        </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                            <div className="card result_card">
                                <h3>History</h3>
                            </div>
                        </div>
                    </div>
                <ReactModal 
                    ariaHideApp={false}
                    isOpen={this.state.showModal}
                    style={{height:10, width:15 }}
                    contentLabel="Minimal Modal Example">
                        <div className="modal-box">
                            <h2>Edit Patient</h2>
                            <div className="col-6">
                                <div className="col-6">
                                    <label>First Name
                                        <input type="text" placeholder="James" />
                                    </label>
                                </div>
                                <div className="col-6">
                                    <label>Last Name
                                        <input type="text" placeholder="Smith" />
                                    </label>
                                </div>
                                <div className="col-6">
                                    <label>D.O.B
                                        <input type="text" placeholder="01/01/62" />
                                    </label>
                                </div>
                                <div className="col-6">
                                    <label>Gender
                                        <select>
                                            <option value="male">Male</option>
                                            <option value="female">Female</option>
                                        </select>
                                    </label>
                                </div>
                            </div>
                            <div className="col-6">
                                <div className="col-4">
                                    <label>Smoker
                                        <select>
                                            <option value="male1">Yes</option>
                                            <option value="female1">No</option>
                                        </select>
                                    </label>
                                </div>
                                <div className="col-4">
                                    <label>Ward
                                        <input type="text" placeholder="7A" />
                                    </label>
                                </div>
                                <div className="col-4">
                                    <label>Height
                                        <input type="text" placeholder="175cm" />
                                    </label>
                                </div>
                            </div>
                            <button className="btn btn-primary">Save</button>
                        </div>
                    <a onClick={this.handleCloseModal} className="close-modal">X</a>
                </ReactModal>           
            </ContentPage>
        );
    }
}

export default PatientsDetails;