import React, {Component} from 'react';
import './../../../styles/login.scss';
import LoginPage from './../../Containers/LoginPage';
import {notify} from './../../../utility';
import {authApi} from './../../../api';

class RestPassword extends Component {

    constructor(props){
        super(props);


        this.handleLoginClick = this.handleLoginClick.bind(this);
        this.emailInput = React.createRef();
    }

    handleLoginClick(e){
        e.preventDefault();
        console.log(this.emailInput);
        const resetInput = {
            email:this.emailInput.current.value,
        }

        if(!resetInput.email){
            notify.error('email is empty!');
            return;
        }

        authApi.sendEmail(resetInput).then(res => {
            if(res.data){
                notify.success('send rest password to email');
                this.props.history.push('/login');
            }else{
                notify.error('send email fail');
            }
        })

    }


    render() {
        return (
            <LoginPage>
                <h3>Reset Password</h3>
                <p>Enter the email address associated with your account, and we’ll email you a link to reset your password</p>
                <input ref={this.emailInput} id="email" type="email" placeholder="Email Address" />
                <button onClick={this.handleLoginClick} type="button" className="btn btn-primary btn-block">Reset Password</button>
            </LoginPage>
        );
    }
}

export default RestPassword;