import React, {Component} from 'react';
import {add_image_icon_svg} from './../../images';
import ContentPage from './../Containers/ContentPage';
import './../../styles/organisationDetails.scss';
import {organisationApi} from './../../api';


//organisationDetails
class OrganisationDetails extends Component {

    constructor(props) {
        super(props);
        this.state = {
			organisationDetails: {},
			userInfo: {},
			cartItem: []
		};
    }

    componentDidMount() {
        organisationApi.organisationDetails().then(res=>{
            setTimeout(() => {
                this.setState({
                    organisationDetails: res.data.organisationDetails,
                    userInfo: res.data.userInfo,
                    cartItem: res.data.cartItem
                  });                
            }, 1000);
        })
      }

    render() {

        const {organisationDetails,
             userInfo,
             cartItem} = this.state;

        return (
            <ContentPage pageTitle="Nursing Home Limited">
                <div className="main_part_header">
                    <div></div>
                    <div>
                        <button className="btn btn-primary mr10">Edit</button>
                        <button className="btn btn-primary">Delete</button>
                    </div>
                </div>
                <div className="dashboard">
                    <div className="dashboard_item dashboard_item-md-4">
                        <div className="card org_info">
                            <a href="/" className="org_image">
                                <img alt="" src={add_image_icon_svg} />
                            </a>
                            <h3>Organistion Name</h3>
                            <h3 className="org_name">{organisationDetails.organistionName}</h3>
                            <table>
                                <tbody>
                                    <tr>
                                        <td className="pb10">Telephone</td>
                                        <td className="pb10">{organisationDetails.telephone}</td>
                                    </tr>
                                
                                    <tr>
                                        <td>Address Line 1</td>
                                        <td>{organisationDetails.addressLine1}</td>
                                    </tr>
                                    <tr>
                                        <td>Address Line 2</td>
                                        <td>{organisationDetails.addressLine2}</td>
                                    </tr>
                                    <tr>
                                        <td>Town/City</td>
                                        <td>{organisationDetails.city}</td>
                                    </tr>
                                    <tr>
                                        <td>Postcode</td>
                                        <td>{organisationDetails.postcode}</td>
                                    </tr>
                                    <tr>
                                        <td className="pb10">Country</td>
                                        <td className="pb10">{organisationDetails.country}</td>
                                    </tr>
                                    <tr>
                                        <td className="pb10">Organisation Type</td>
                                        <td className="pb10">{organisationDetails.organisationType}</td>
                                    </tr>
                                    <tr>
                                        <td>Email</td>
                                        <td>{organisationDetails.email}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                    {
                        cartItem.map((item) => {
                            return (
                                <div key={`key${item.id}`} className="dashboard_item">
                                    <div className="card stat">
                                        <strong>{item.number}</strong> {item.title}
                                    </div>
                                </div>
                            )
                        })
                    }
                    <div className="dashboard_item dashboard_item-md-4">
                        <div className="card admin_info">
                            <p>Admin for Organistion</p>
                            <table>
                                <tbody>
                                    <tr>
                                        <td>First Name</td>
                                        <td>{userInfo.firstName}</td>
                                    </tr>
                                    <tr>
                                        <td>Last Name</td>
                                        <td>{userInfo.lastName}</td>
                                    </tr>
                                    <tr>
                                        <td>Email</td>
                                        <td>{userInfo.email}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </ContentPage>
        );
    }
}

export default OrganisationDetails;