
import React, { Component } from 'react';
import './../../styles/dashboard.scss';

import {
    logo_splash_png,
    user_circle_o_svg,
    icon_logout_svg
} from './../../images';

import {
    Link
} from 'react-router-dom';

class header extends Component {
    render() {
        return (
            <header className="admin_header">
            <a href="/dashboard" className="logo">
                <img src={logo_splash_png} alt="Viscgo" />
            </a>
            <div className="toolbar">
                <div>April 12, 2019</div>
                <div className="avatar_image">
                    <img src={user_circle_o_svg} alt="user" />
                </div>
                <div className="user_menu dropdown">
                    <Link className="username_text dropdown-toggle" to="/profile">
                        ADMIN
                    </Link>
                    <br />
                    Mike Smith
                    <div className="dropdown-menu dropdown-menu-left">
                        <a className="dropdown-item" href="/">Edit Profile</a>
                        <a className="dropdown-item blue" href="/">
                            Logout <img alt="" src={icon_logout_svg} />
                        </a>
                    </div>
                </div>
            </div>
        </header>
        );
    }
}

export default header;