import React, {Component} from 'react';
import './../../../styles/login.scss';
import {Link} from 'react-router-dom';
import LoginPage from './../../Containers/LoginPage';
import {notify} from './../../../utility';
import {authApi} from './../../../api';

class Login extends Component {

    constructor(props){
        super(props);

        this.handleLoginClick = this.handleLoginClick.bind(this);
        this.usernameInput = React.createRef();
        this.passwordInput = React.createRef();
    }

    componentDidMount(){
        notify.info('username: admin, password: admin');
    }

    handleLoginClick(e){
        e.preventDefault();

        const loginInfo = {
            username:this.usernameInput.current.value,
            password:this.passwordInput.current.value,
        }

        if(!loginInfo.username){
            notify.error('username is empty!');
            return;
        }
        if(!loginInfo.password){
            notify.error('password is empty!');
            return;
        }

        authApi.login(loginInfo).then(res => {
            if(res.data){
                notify.success('success, welcome');
                this.props.history.push('/dashboard');
            }else{
                notify.error('user name or password invalid');
                notify.info('username: admin, password: admin');
            }
        })
    }

    render() {
        return (
            <LoginPage>
                <input ref={this.usernameInput} id="userName" name="username" type="text" placeholder="Username" />
                <input ref={this.passwordInput} id="password" name="password" autoComplete="false" type="password" placeholder="Password" />
                <hr />
                <button type="button" onClick={this.handleLoginClick} className="btn btn-primary btn-block">
                        Login
                </button>
                <p className="forget-link">Did you <Link to="/restPassword">forget your password?</Link></p>
            </LoginPage>
        );
    }
}

export default Login;