import React, {Component} from 'react';
import ContentPage from './../Containers/ContentPage';
import {button_plus_svg, search_svg} from './../../images';
import {staffApi} from './../../api';
import ReactPaginate from 'react-paginate';

class Staff extends Component {

    constructor(props) {
        super(props);
        this.state = {
            data: [],
        };
    }

    componentDidMount() {
        this.loadCommentsFromServer(0);
    }

    handlePageClick = data => {
        this.loadCommentsFromServer(data.selected);
    };

    loadCommentsFromServer(page) {
        staffApi.staffData(page).then(res => {
            this.setState({
                data: res.data.data,
                pageCount: res.data.total_pages,
            });
        })
    }

    render() {
        return (
            <ContentPage pageTitle="Staff">
                <div className="dashboard">
                    <div className="dashboard_item dashboard_item-full">
                        <div className="card">
                            <div className="main_part_header">
                                <div>
                                    <button className="btn btn-primary">Add Staff
                                        <img src={button_plus_svg} alt="" />
                                    </button>
                                </div>
                                <div className="search-box">
                                    <input type="text" className="search-input" />
                                    <img src={search_svg} alt="" className="search-icon" />
                                </div>
                            </div>
                            <div className="content">

                                <div className="table-responsive-md">
                                    <table className="table">
                                        <thead className="thead-light">
                                            <tr>
                                                <th scope="col">First Name</th>
                                                <th scope="col">Last Name</th>
                                                <th scope="col">Permissions</th>
                                                <th scope="col">Status</th>
                                                <th scope="col"></th>
                                                <th scope="col">Action</th>
                                            </tr>
                                        </thead>
                                        <tbody>

                                            {
                                                this.state.data.map((row, index) => {
                                                    return(
                                                        <tr key={`organisationTableKey${index}`}>
                                                            <td>{`${row.firstName} ${row.id}`}</td>
                                                            <td>{row.lastName}</td>
                                                            <td>{row.permissions}</td>
                                                            <td>{row.status}</td>
                                                            <td><a href="/" className="btn btn-primary">View</a></td>
                                                            <td className="action-td dropdown">
                                                                <a href="/">•••</a>
                                                                <div className="dropdown-menu">
                                                                    <a className="dropdown-item" href="/">Edit</a>
                                                                    <a className="dropdown-item" href="/">Delete</a>
                                                                </div>
                                                            </td>
                                                        </tr>
                                                    )
                                                })
                                            }
                                            <tr>

                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                                <nav>
                                    <ReactPaginate
                                        previousLabel={'Previous'}
                                        nextLabel={'Next'}
                                        breakLabel={'...'}
                                        breakClassName={'page-item disabledme'}
                                        pageCount={this.state.pageCount}
                                        marginPagesDisplayed={2}
                                        pageRangeDisplayed={5}
                                        onPageChange={this.handlePageClick}
                                        containerClassName={'pagination'}
                                        subContainerClassName={'page-item page-number'}
                                        activeClassName={'active'}
                                    />
                                </nav>
                            </div>
                        </div>
                    </div>
                </div>
            </ContentPage>
        );
    }
}

export default Staff;