import React, {Component} from 'react';
import ContentPage from './../Containers/ContentPage';
import {profileApi} from './../../api';

class Profile extends Component {

    constructor(props){
        super(props);
        this.state = {
            userInfo:{}
        }
    }

    componentDidMount(){
        profileApi.userInfo().then(res => {
            setTimeout(() => {
                this.setState({
                    userInfo:res.data
                });
            }, 1000);
        });
    }

    render() {
        const {userInfo} = this.state;
        return (
            <ContentPage pageTitle="Profile">
                <div className="dashboard">
                    <div className="dashboard_item dashboard_item-full">
                        <div className="card">
                            <div className="main_part_header">
                                <div>
                                    <h2>{userInfo.firstName} {userInfo.lastName} <span>- [{userInfo.role}]</span></h2>
                                </div>
                                <div>
                                    <button className="btn btn-primary">Edit</button>
                                </div>
                            </div>
                            <div className="content terms-settings">
                                <div className="setting-col">
                                    <div className="block">
                                        <p>First Name</p>
                                        <h3>{userInfo.firstName}</h3>
                                    </div>
                                    <div className="block">
                                        <p>Last Name</p>
                                        <h3>userInfo.lastName</h3>
                                    </div>
                                    <div className="block">
                                        <p>Administration Level</p>
                                        <h3>{userInfo.role}</h3>
                                    </div>
                                </div>
                                <div className="setting-col">
                                    <div className="block">
                                        <p>Email Address</p>
                                        <h3>{userInfo.email}</h3>
                                    </div>
                                    <div className="block">
                                        <p>Password</p>
                                        <h3>{userInfo.password}</h3>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </ContentPage>
        );
    }
}

export default Profile;