
import React, { Component } from 'react';
import {
    Route,
    Link
} from 'react-router-dom';

import {
    dash_svg,
    room_booking_svg,
    events_svg,
    user_svg,
    settings_svg,
} from './../../images';

class Navigation extends Component {
    render() {
        return (
            <nav className="admin_nav">
                <ul className="menu">
                    <Nav to="/dashboard" activeOnlyWhenExact={true}>
                        <img alt="" src={dash_svg} />Dashboard
                    </Nav>
                    <Nav to="/organisation">
                        <img alt="" src={room_booking_svg} />Organisations
                    </Nav>
                    <Nav to="/staff">
                        <img alt="" src={events_svg} />Staff
                    </Nav>
                    <Nav to="/patients">
                        <img alt="" src={user_svg} />Patients
                    </Nav>
                    <Nav to="/settingTerms">
                        <img alt="" src={settings_svg} />Settings
                    </Nav>                                        
                </ul>
            </nav>
        );
    }
}

class Nav extends Component {
    render() {
            const {
                to,
                activeOnlyWhenExact,
                children
            } = this.props;
            return (
                    <Route path={to} exact={activeOnlyWhenExact} children={({ match }) => (
                        <li className="menu_item">
                            <Link className={match ? 'menu_link active' : 'menu_link'} to={to}>
                                {children}
                            </Link>
                        </li>                    
                    )}/>
        );
    }
}

export default Navigation;