import React, {
    Component
} from 'react';

import ContentPage from './../Containers/ContentPage';
import {dashboaedApi} from './../../api';

class Dashboard extends Component {

    constructor(props) {
        super(props);

        this.state = {
            dashboardData: []
        };
    }

    componentDidMount() {
        dashboaedApi.dashboardData().then(res => {
            console.log('object :', res);
            setTimeout(() => {
                this.setState({
                    dashboardData: res.data,
                })
            }, 1000);
        });
    }

    render() {
        console.log(this.state.dashboardData);
        return (
            <ContentPage pageTitle="Dashboard">
                <div className="dashboard">
                    {
                        this.state.dashboardData.map((data, index) => {
                            return (
                                <div key={`dashboardItemKey${index}`} className="dashboard_item">
                                    <div className="card stat">
                                        <strong>{data.number}</strong> {data.title}
                                    </div>
                                </div>
                            )
                        })
                    }
                </div>
            </ContentPage>
        );
    }
}

export default Dashboard;