import React, {
    Component
} from 'react';
import PropTypes from 'prop-types';

import {
    dash_home_bread_crumb_svg,
} from './../../images';

import Header from './../Containers/Header';
import Navigation from './../Containers/Navigation';

class ContentPage extends Component {

    static propTypes = {
        pageTitle: PropTypes.string.isRequired,
    };

    render() {
        
        const {
            pageTitle,
            children
        } = this.props;

        return (
            <div className="admin">
                <Header />
                <Navigation />
                <main className="admin_main">
                    <h2>{pageTitle}</h2>
                    <h4 style={{marginBottom: 50}}>
                        <img src={dash_home_bread_crumb_svg} alt="" />
                        &nbsp;• <a href="/" className="text-underline">{pageTitle}</a>
                    </h4>
                    {children}
                </main>
            </div>
        );
    }
}

export default ContentPage;