import React, {Component} from 'react';
import ReactPaginate from 'react-paginate';
import {Link} from 'react-router-dom';
import ContentPage from './../Containers/ContentPage';
import {button_plus_svg,search_svg} from './../../images';
import {organisationApi} from './../../api';

class Organisation extends Component {

    constructor(props) {
        super(props);
        this.state = {
            data: [],
        };
    }

    componentDidMount() {
        this.loadCommentsFromServer(0);
      }

    handlePageClick = data => {
        this.loadCommentsFromServer(data.selected);
    };

    loadCommentsFromServer(page) {
        organisationApi.organisationData(page).then(res=>{
            this.setState({
                data: res.data.data,
                pageCount: res.data.total_pages,
              });
        })
    }

    render() {
        return (
            <ContentPage pageTitle="Organisation">
                <div className="dashboard">
                    <div className="dashboard_item dashboard_item-full">
                        <div className="card">
                            <div className="main_part_header">
                                <div>
                                    <Link to="/organisationDetails" className="btn btn-primary">Add Organisation
                                        <img src={button_plus_svg} alt="" />
                                    </Link>
                                </div>
                                <div className="search-box">
                                    <input type="text" className="search-input" />
                                    <img src={search_svg} alt="" className="search-icon" />
                                </div>
                            </div>
                            <div className="content">
                                <div className="table-responsive-md">
                                    <table className="table">
                                        <thead className="thead-light">
                                            <tr>
                                                <th scope="col">Date Registered</th>
                                                <th scope="col">Organisation Name</th>
                                                <th scope="col">Telephone</th>
                                                <th scope="col">City</th>
                                                <th scope="col">Status</th>
                                                <th scope="col"></th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {
                                                this.state.data.map((row, index) => {
                                                    return(
                                                        <tr key={`organisationTableKey${index}`}>
                                                            <td>{row.dataRegister}</td>
                                                            <td><Link to="/organisationDetails">{row.organisationName}</Link></td>
                                                            <td>{row.telephone}</td>
                                                            <td>Manchester</td>
                                                            <td>{row.status}</td>
                                                            <td><Link to="/organisationDetails" className="btn btn-primary">View</Link></td>
                                                        </tr>
                                                    )
                                                })
                                            }
                                        </tbody>
                                    </table>
                                </div>
                                <nav>
                                    <ReactPaginate
                                        previousLabel={'Previous'}
                                        nextLabel={'Next'}
                                        breakLabel={'...'}
                                        breakClassName={'page-item disabledme'}
                                        pageCount={this.state.pageCount}
                                        marginPagesDisplayed={2}
                                        pageRangeDisplayed={5}
                                        onPageChange={this.handlePageClick}
                                        containerClassName={'pagination'}
                                        subContainerClassName={'page-item page-number'}
                                        activeClassName={'active'}
                                    />
                                </nav>
                            </div>
                        </div>
                    </div>
                </div>
            </ContentPage>
        );
    }
}

export default Organisation;